import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CommonService } from 'app/@theme/services/common.service';
import { ProductService } from 'app/@theme/services/product.service';

@Component({
  selector: 'ngx-place-order',
  templateUrl: './place-order.component.html',
  styleUrls: ['./place-order.component.scss']
})
export class PlaceOrderComponent implements OnInit {

  productList :any = [];
  isAdmin:boolean = false;
  constructor(private productService:ProductService, private route:Router, private commonService: CommonService) {
    this.commonService.setTitle('Product List');
   }

  ngOnInit(): void {
    this.checkIsAdmin();
    this.getAllProductList();
  }

  goToPlaceOrder(){
    this.route.navigate(['pages/order/add']);
  }

  goToAdd(){
    this.route.navigate(['pages/product/add']);
  }

  checkIsAdmin(){
    if(this.commonService.getUser().name == 'ADMIN')
      this.isAdmin = true;
    else
      this.isAdmin = false;
  }

  getAllProductList(){
    this.productService.getAllProductList().subscribe(
      data=>{
        if(data['success']){
          this.productList = data['data'];
        }
      },error=>{

      }
    )
  }

  clickAddProduct(){
    this.route.navigate(['pages/product/add'])
  }

  order(id){
    let userName = this.commonService.getUser().name;
    if(userName != "ADMIN")
      this.route.navigate(['pages/order/add'])
  }

  addPrices(id){
    this.route.navigate(['pages/product/addPrice',id]);
  }

  goToEdit(id){
    this.route.navigate(['pages/product/edit',id]);
  }

  

}
