import { Component, OnInit } from "@angular/core";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { ConfirmationDialogComponent } from "app/@theme/components/confirmation-dialog/confirmation-dialog.component";
import { PlaceOrder } from "app/@theme/model/order";
import { CommonService } from "app/@theme/services/common.service";
import { OrderService } from "app/@theme/services/order.service";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "ngx-order-status",
  templateUrl: "./order-status.component.html",
  styleUrls: ["./order-status.component.scss"],
})
export class OrderStatusComponent implements OnInit {

  ordeList: PlaceOrder[] = [];
  isAdmin: boolean = false;
  confirmDialogMsg:string = '';

  constructor(
    private orderService: OrderService,
    private commonService: CommonService,
    private modalService: NgbModal,
    private toastr: ToastrService
  ) {
    this.commonService.setTitle('Orders');
  }

  ngOnInit(): void {
    if (this.commonService.getUser().name == "ADMIN") {
      this.isAdmin = true;
      this.getAllOrderList();
    } else {
      this.isAdmin = false;
      this.getOrderListByPartyId();
    }
  }

  getAllOrderList() {
    this.orderService.getAllOrders().subscribe(
      (data) => {
        if (data["success"]) this.ordeList = data["data"];
      },
      (error) => {}
    );
  }

  getOrderListByPartyId() {
    let partyId = this.commonService.getUser().userId;
    this.orderService.getOrderListByPartyId(partyId).subscribe(
      (data) => {
        if (data["success"]) this.ordeList = data["data"];
      },
      (error) => {}
    );
  }

  updateStatus(id, status) {
    if(status != 'Canceled')
      this.confirmDialogMsg = 'update status?';
    else
    this.confirmDialogMsg = 'cancel order?';
    const modalRef = this.modalService.open(ConfirmationDialogComponent, {
      size: "sm",
    });
    modalRef.componentInstance.confirmDialogMsg = this.confirmDialogMsg
    modalRef.result.then((result) => {
      if (result) {
        this.orderService.updateStatus(id, status).subscribe((data) => {
          if (data["success"]) this.toastr.success("Marked as " + status);
          if (this.isAdmin) this.getAllOrderList();
          else this.getOrderListByPartyId();
        });
      }
    });
  }
}
