import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { CommonService } from "app/@theme/services/common.service";
import { OrderService } from "app/@theme/services/order.service";
import { ToastrService } from "ngx-toastr";
import { PlaceOrder } from "../../../@theme/model/order";

@Component({
  selector: "ngx-make-order",
  templateUrl: "./make-order.component.html",
  styleUrls: ["./make-order.component.scss"],
})
export class MakeOrderComponent implements OnInit {
  capacityList: any = [];
  qualityList: any = [];
  typeList: any = [];
  packageSizeList: any = [];
  placeOrder: PlaceOrder = new PlaceOrder();
  formSubmitted: boolean = false;
  currentOrder:Number;
  constructor(
    private orderService: OrderService,
    private toastr: ToastrService,
    private route: Router,
    private commonService:CommonService,
    private _route: ActivatedRoute
  ) {
    this.commonService.setTitle('Place Order');
  }

  ngOnInit(): void {
    this.placeOrder.quantity=1;
    this.currentOrder = Number(this._route.snapshot.paramMap.get('id'));
    this.getAllCapacityList();
    this.getAllQuality();
    this.getAllPackageSize();
    this.getAllTypes();
    if(this.currentOrder)
    this.getOrderById();
  }

  getAllCapacityList() {
    this.orderService.getAllCapacity().subscribe(
      (data) => {
        this.capacityList = data["data"];
      },
      (error) => {}
    );
  }

  getAllQuality() {
    this.orderService.getAllQuality().subscribe(
      (data) => {
        this.qualityList = data["data"];
      },
      (error) => {}
    );
  }

  getAllPackageSize() {
    this.orderService.getAllPackageSize().subscribe(
      (data) => {
        this.packageSizeList = data["data"];
      },
      (error) => {}
    );
  }

  getAllTypes() {
    this.orderService.getAllType().subscribe(
      (data) => {
        this.typeList = data["data"];
      },
      (error) => {}
    );
  }

  getOrderById(){
    this.orderService.getById(this.currentOrder).subscribe(
      data=>{
        if(data['success']){
          this.placeOrder = data['data'];
        }
       // else
        //this.toastr.error("No data found")
      }
    )
  }

  updateOrder(myForm){
    this.formSubmitted = true;
    if(myForm.valid){
      this.orderService.updateOrder(this.placeOrder).subscribe(
        (data) => {
          if(data['success']){
            //this.toastr.success("Updated successfully");
            this.route.navigate(['pages/cart']);
          }
          //else
          //this.toastr.success(data['msg']);
        },
        (error) => {
          //this.toastr.error('Error placing order')
        }
      );
    }
  }

  incrQty(){
    this.placeOrder.quantity++;
  }

  decrQty(){
    if(this.placeOrder.quantity != 1)
    this.placeOrder.quantity--;
  }

  addToCart(myForm){
    this.formSubmitted = true;
    if(myForm.valid){
      this.placeOrder.partyId = Number(this.commonService.getUser().userId);
      this.placeOrder.id = 0;
      this.placeOrder.isCart = true;
      this.placeOrder.orderStatus = '';
      this.orderService.addToCart(this.placeOrder).subscribe(
        (data) => {
          if(data['success']){
            this.route.navigate(['pages/cart']);
            //this.resetForm(myForm);
          }
          //else
          //this.toastr.success(data['msg']);
        },
        (error) => {
          //this.toastr.error('Error placing order')
        }
      );
    }
  }

  placeNewOrder(myForm) {
    this.formSubmitted = true;
    if (myForm.valid) {
      this.placeOrder.partyId = Number(this.commonService.getUser().userId);
      this.orderService.placeOrder(this.placeOrder).subscribe(
        (data) => {
          if(data['success']){
            //this.toastr.success("Order placed successfully");
            this.resetForm(myForm);
          }
          //else
          //this.toastr.success(data['msg']);
        },
        (error) => {
          //this.toastr.error('Error placing order')
        }
      );
    }
  }

  resetForm(myForm){
    this.formSubmitted = false;
    myForm.reset();
    this.placeOrder = new PlaceOrder();
    this.placeOrder.quantity = 1;
  }
}
